.list{
    color: white;
    width: 300px;
    background-color: #bf07ac;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}
.list p{
   padding: 10px; 
   position: relative;
   transition: ease-in-out;

}
.list p input{
    background-color: transparent;
    background-blend-mode: screen;
    border: 0;
    color: whitesmoke;
    font-size: 18px;
    font-style: oblique;
    font-weight: 400;

}
.list p input:focus{
    outline: none;
}
.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
.faicons{
    margin: 0.5px;
    font-size: 15px;
}