body{
  background-color: #9266ff;

}
.App{
  background-color:#3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
   
}
#to-do-form input{
  background-color: rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 500 px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;

}

#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus, #to-do-form button:focus{
  outline:none;
}
#to-do-form button{
  border: 50;
  width: 80px;
  height: 300 px;
  padding: 60 20px;
  margin: 20px;
  border-radius: 50px;
  border-color: white;
  background-color: rgb(202,156,24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;


}

